.primary_checkbox {
  display: inline-block;
  position: relative;
  width: 18px;
  height: 18px;
  margin: 0;
  flex: 18px 0;
  line-height: 18px;
}
.primary_checkbox .checkmark {
  position: relative;
  width: 18px;
  height: 18px;
  top: 0;
  left: 0;
  display: block;
  cursor: pointer;
  line-height: 18px;
  flex: 18px 0;
  border-radius: 50%;
}
.primary_checkbox input {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
}
.primary_checkbox input:checked ~ .checkmark:after {
  width: 100%;
  height: 100%;
  border: 0;
  transition: 0.3s;
  -webkit-transform: scale(0);
          transform: scale(0);
}
.primary_checkbox input:checked ~ .checkmark {
  background: #3b76ef !important;
  box-shadow: 0 5px 10px rgba(59, 118, 239, 0.3) !important;
  transition: 0.3s;
}
.primary_checkbox input:checked ~ .checkmark::before {
  content: "\E64C";
  font-family: themify;
  position: absolute;
  display: block;
  top: 0;
  left: 3px;
  text-indent: 1px;
  color: #828bb2;
  background-color: transparent;
  border: 0;
  -webkit-transform: rotate(8deg);
  transform: rotate(8deg);
  font-size: 10px;
  font-weight: 600;
  line-height: 18px;
  z-index: 99;
  color: #fff;
  transition: 0.3s;
}
.primary_checkbox .checkmark:after {
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  content: "";
  width: 18px;
  height: 18px;
  background: 0 0;
  border-radius: 50px;
  border: 1px solid #828bb2;
  transition: 0.3s;
  -webkit-transform: scale(1);
          transform: scale(1);
}

